<nav class="main-breadcrumb" aria-label="breadcrumb" v-if="settingsData" :style="{backgroundImage: `url(${settingsData.breadcrumb_bg})`}"><div class="container"> <div class="breadcrumb-title"><h2>{{ $t("wishlist") }}</h2></div><nav aria-label="breadcrumb"><ol class="breadcrumb"><li class="breadcrumb-item"><router-link to="/" exact>{{ $t("home") }}</router-link></li><li class="breadcrumb-item active" aria-current="page">{{ $t("wishlist") }}</li></ol></nav></div></nav><!--breadcrumb--><!--wishlist--><section class="wishlist"><div class="container"><div class="wishlist-content"><div class="table-responsive table-area"><h2 v-if="wishlist.length == 0">no data</h2><table class="table table-hover" v-if="wishlist.length &gt; 0"><thead><tr><th>product</th><th>adding date</th><th>controle</th></tr></thead><tbody><tr v-for="product in wishlist" :key="product.id"><td> <a class="product-name" href="single-product.html"><div class="image"><img class="img-fluid" :alt="product.image_alt" :src="product.image"></div><p>{{ product.title }}</p></a></td><td>{{ product.created_at }}</td><td><form enctype="multipart/form-data" @submit.prevent="removeFromWishlist"><input name="product_id" :value="product.id" type="hidden"><button class="btn remove removeFromProfile" type="submit"></button></form></td></tr></tbody></table></div></div></div></section><!--wishlist-->